<template>
    <div>

        <vs-button size="small" @click="turnOn('all')" class="mr-1">Turn ON All</vs-button>

        <vs-button size="small" @click="turnOff('all')" class="mr-1">Turn OFF All</vs-button>


        <div v-for="(section, index) in $store.state.modules.sections">
            <vs-divider>
                {{section.title}}
            </vs-divider>

            <vs-row>
                <vs-col type="flex" vs-justify="center" vs-align="center" vs-sm="12" vs-lg="3" v-for="(module, index) in section.modules">
                    <vs-card actionable @click.native="selectModule(module.id)">
                        <div class="card-content-section">
                            <h5 class="mb-1">{{ module.alias }}</h5>
                            ID: {{ module.did }}
                        </div>
                    </vs-card>
                </vs-col>
            </vs-row>
        </div>

    </div>
</template>

<script>
    export default {
        methods: {
            selectModule(id){
                console.log(id)
            },
            turnOn(type){
                if(type === 'all'){
                    this.$http.get('/turn/on/all').then(() => {
                        console.log('turn on all')
                    })
                }
            },
            turnOff(type){
                if(type === 'all'){
                    this.$http.get('/turn/off/all').then(() => {
                        console.log('turn off all')
                    })
                }
            }
        }
    }
</script>

